import { defineComponent, ref, watch } from '@nuxtjs/composition-api';
import moment from 'moment';
export default defineComponent({
    props: {
        wAttr: {
            type: Object,
            default: () => ({})
        },
        defaultValue: {
            type: String,
            default: ''
        }
    },
    setup(props, { emit }) {
        const modal = ref(false);
        const time = ref(props.defaultValue);
        const timeField = ref(props.defaultValue);
        watch(() => time.value, () => {
            timeField.value = time.value;
            emit('data', time.value);
        });
        const onChangeTime = () => {
            if (moment(timeField.value, 'HH:mm', true).isValid()) {
                time.value = timeField.value;
            }
            else {
                time.value = null;
            }
        };
        const setTimeNow = () => {
            const timeNow = moment().format('HH:mm');
            time.value = timeNow;
            timeField.value = timeNow;
        };
        const timeFieldRules = [
            (v) => !!v || 'Time is required',
            (v) => moment(v, 'HH:mm', true).isValid() || 'Invalid time'
        ];
        return {
            modal,
            time,
            timeField,
            onChangeTime,
            setTimeNow,
            timeFieldRules
        };
    }
});
